<template>
    <form class="search-form" autocomplete="off" :class="inputClass" v-if="fields" @submit.prevent="handleInput">
        <div class="row">
            <div class="col-md-12">
                <div class="radio-full">
                    <label class="radio-inline" v-bind:for="'rbTripType'+ftIndex"
                           v-for="(ft,ftIndex) in fields.TripTypes" :key="ftIndex">
                        <input type="radio" v-bind:id="'rbTripType'+ftIndex" v-bind:value="ft.value"
                               v-model="inpTripType">
                        {{ft.title}}
                    </label>
                    <label class="checkbox-inline" for="chkDirectFlight">
                        <input type="checkbox" id="chkDirectFlight" v-model="qp.directFlights"
                               :checked="qp.directFlights">
                        {{ fields.directFlights.title }}</label>
                </div>
            </div>
        </div>
        <div class="row search-fields">
            <div class="col-md-12">
                <div class="row">
                    <div class="form-elements col-md-2 p-0">
                        <label for="fromAirport">{{ fields.fromCity.title }}</label>
                        <div class="form-item pin-field">
                            <i class="awe-icon awe-icon-marker-1"></i>
                            <AirportSearch ref="fromAirport" v-model="fromSearch" :options="optionsFrom" :onItemSelected="onFromSelected"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="form-elements col-md-2 p-0">
                        <label for="toAirport">{{ fields.toCity.title }}</label>
                        <div class="form-item pin-field">
                            <i class="awe-icon awe-icon-marker-1"></i>
                            <AirportSearch ref="toAirport" v-model="toSearch" :options="optionsTo" :onItemSelected="onToSelected"
                                           :onInputChange="onCountryInputChange"/>
                        </div>
                    </div>
                    <div class="form-elements col-md-2 p-0">
                        <label for="dateDeparture">{{ fields.departure.title }}</label>
                        <div class="form-item date-picker">
                            <i class="awe-icon awe-icon-calendar"></i>
                            <datepicker id="dateDeparture" v-bind:placeholder="fields.departure.placeholder"
                                        autocomplete="off" :disabled-dates="departureMinDate" :required="true"
                                        v-model="inpDepartureDate" @selected="dateSelectedFunction"
                                        :format="getYYYYMMDD"></datepicker>
                        </div>
                    </div>
                    <div class="form-elements col-md-2 p-0">
                        <label for="dateReturn">{{ fields.retrun.title }}</label>
                        <div class="form-item date-picker">
                            <i class="awe-icon awe-icon-calendar"></i>
                            <datepicker id="dateReturn" v-bind:placeholder="fields.retrun.placeholder"
                                        :disabled="waitingForDepDate" :disabled-dates="returnMinDate"
                                        :required="!isOneWayTrip"
                                        v-model="inpReturnDate" @selected="dateSelectedFunction"
                                        :format="getYYYYMMDD" ref="dateReturnObj"></datepicker>
                        </div>
                    </div>
                    <div class="form-elements col-md-2 p-0">
                        <label for="cabinType">{{ fields.cabinTypes.title }}</label>
                        <select class="awe-select-xxx" id="cabinType" v-model="qp.cabinType">
                            <option v-for="(ct,ctIndex) in fields.cabinTypes.values" v-bind:value="ct.value"
                                    :key="ctIndex">
                                {{ct.title}}
                            </option>
                        </select>
                    </div>
                    <div class="form-elements col-md-2 p-0">
                        <div class="row">
                            <div class="form-elements col-md-3 p-0">
                                <label for="adults">{{ fields.adults.title }}</label>
                                <select class="awe-select-xxx" id="adults" v-model="qp.adults" v-on:change="updateHeads">
                                    <option v-for="(a,aIndex) in fields.adults.values" v-bind:value="a.value"
                                            :key="aIndex">
                                        {{a.title}}
                                    </option>
                                </select>
                            </div>
                            <div class="form-elements col-md-3 p-0">
                                <label for="childs">{{ fields.childs.title }}</label>
                                <select class="awe-select-xxx" id="childs" v-model="qp.childs" v-on:change="updateHeads">
                                    <option v-for="(c,cIndex) in fields.childs.values" v-bind:value="c.value"
                                            :key="cIndex">
                                        {{c.title}}
                                    </option>
                                </select>
                            </div>
                            <div class="form-elements col-md-3 p-0">
                                <label for="infants">{{ fields.infants.title }}</label>
                                <select class="awe-select-xxx" id="infants" v-model="qp.infants">
                                    <option v-for="(i,iIndex) in fields.infants.values" v-bind:value="i.value"
                                            :key="iIndex">
                                        {{i.title}}
                                    </option>
                                </select>
                            </div>
                            <div class="form-elements col-md-3 p-0">
                                <input class="searchbutton" type="submit" value="Find" v-bind:disabled="totalHeads > 9">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </form>
</template>
<script>
    import Datepicker from 'vuejs-datepicker'
    import AirportSearch from "./AirportSearch";
    import * as DAO from '../api/apiCalls.js';
    import {
        getYYYYMMDD,
        addDaysToDate,
        minusDaysToDate,
        formatDate,
        getTime,
        getDateFromString,
        getFlightsQueryString,
        getFlightObject,
        isLocalHost
    } from '../api/utils.js';

    export default ({
        name: "FlightSearchNew",
        components: {
            Datepicker,
            AirportSearch,
        },
        props: {
            onSubmitClick: {
                type: Function,
            },
            inputClass: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                inpTripType: "0",
                inpDepartureDate: '',
                inpReturnDate: '',
                waitingForDepDate: true,
                fields: false,
                optionsFrom: {
                    placeholder: "",
                    id: "fromAirport",
                    required: true
                },
                optionsTo: {
                    placeholder: "",
                    id: "toAirport",
                    required: true
                },
                departureMinDate: {
                    to: minusDaysToDate(new Date(), 1),
                },
                returnMinDate: {
                    to: minusDaysToDate(new Date(), 1),
                },
                qp: {
                    from: '',
                    to: '',
                    departureDate: '',
                    returnDate: '',
                    directFlights: false,
                    adults: 1,
                    childs: 0,
                    infants: 0,
                    tripType: 0,
                    cabinType: 5,
                },
                toSearch: '',
                fromSearch: '',
                totalHeads: 0
            }
        },
        computed: {
            isOneWayTrip: function () {
                return this.qp.tripType === 0;
            },
        },
        methods: {
            getYYYYMMDD,
            addDaysToDate,
            minusDaysToDate,
            formatDate,
            getTime,
            async getAirports(query) {
                let queryLC = query.toLowerCase();
                let arr = this.$store.getters.getAirport.filter(o=> o.iata !== null && o.name !== null);
                return arr.filter(o => o.iata.toLowerCase().indexOf(queryLC)>=0 || o.name.toLowerCase().indexOf(queryLC)>=0).sort(o=>o.name);
            },
            /** use the following function to server from API **/
            async getAirportsFromApi(query){
                const result = await DAO.getAirports(query);
                return result;
            },
            async onCountryInputChange(query) {
                if (query.trim().length <= 2) {
                    return null
                }
                const result = await this.getAirports(query);
                return result;
            },
            onFromSelected(item) {
                this.qp.from = item.iata;
                this.fromSearch = item.name;
            },
            onToSelected(item) {
                this.qp.to = item.iata;
                this.toSearch = item.name;
            },
            updateHeads() {
                this.totalHeads = parseInt(this.qp.adults, 10) + parseInt(this.qp.childs, 10);
                if(this.totalHeads > 9){
                    alert("You can't search for more than 9 seats");
                }
            },
            dateSelectedFunction(dateSelected) {
                /*console.log("date selected called");
                return moment(dateSelected).format('YYYY-MM-DD');*/
            },
            handleInput(event) {
                event.preventDefault();
                let url = getFlightsQueryString(this.qp, '/flight');
                if (this.$route.path !== "/flight") {
                    this.$router.push(url);
                } else {
                    this.$router.replace(url);
                    this.$emit('onSubmitClick');
                }
            },
            async updateComputedParams() {
                let querySting = this.$route.query;
                if (Object.keys(querySting).length > 0) {
                    if (this.qp.departureDate !== '') {
                        this.inpDepartureDate = getDateFromString(this.qp.departureDate);
                    }
                    if (this.qp.returnDate !== '') {
                        this.inpReturnDate = getDateFromString(this.qp.returnDate);
                    }
                    if(this.qp.from !== ''){
                        const result = await this.getAirports(this.qp.from);
                        this.onFromSelected(result[0]);
                    }
                    if(this.qp.to !== ''){
                        const result = await this.getAirports(this.qp.to);
                        this.onToSelected(result[0]);
                    }
                }else {
                    this.inpReturnDate = null;
                    this.inpDepartureDate = null;
                    this.fromSearch = '';
                    this.toSearch = '';
                }
            },
        },
        beforeCreate() {
            DAO.getFieldsData().then(response => {
                this.fields = response.data;
                this.qp.directFlights = response.data.directFlights.value;
                this.optionsFrom.placeholder = response.data.fromCity.placeholder;
                this.optionsTo.placeholder = response.data.toCity.placeholder;
            });
        },
        mounted() {
            let querySting = this.$route.query;
            this.qp = getFlightObject(querySting);
            this.updateComputedParams();
        },
        watch: {
            inpTripType: function (newValue) {
                if (newValue === "0") {
                    this.qp.returnDate = '';
                    this.inpReturnDate = null;
                    this.$refs.dateReturnObj.clearDate();
                }
                this.qp.tripType = newValue;
            },
            inpDepartureDate: function (newValue) {
                this.waitingForDepDate = false;
                this.qp.departureDate = getYYYYMMDD(newValue);
                this.returnMinDate.to = addDaysToDate(newValue, 1);
            },
            inpReturnDate: function (newValue) {
                if (newValue === null) {
                    this.waitingForDepDate = true;
                } else {
                    this.inpTripType = "1";
                    this.qp.returnDate = getYYYYMMDD(newValue);
                }
            }
        },
    });
</script>

<style lang="scss">
    @import "../scss/FlightSearch.scss";
</style>
